html, body{
    margin:0;
    padding:0;
    height:100%;
}

.layout{
    position: fixed;
    height:100%;
    width :100%;
    background-image:url(./img/pattern.png);
    z-index: 0;
    display: flex;
}
/* canvas 위치 조정 */
.center{
    position: fixed;
    top:4vh;
    left:0vw;
    width: 100%;
    height: 100%;
    z-index: 0;
   
}
.mainContainer{
    display: fixed;
    height: calc(100% - 100px);
    width: calc(100%) ;
}
