.editorMenubar{
    justify-content: center;
    align-items: center;
    visibility: visible;
    white-space: nowrap;

    position: fixed;
    top: calc(100vh - 55px);
    bottom:0;
    z-index: 50;
    left:35vw;
    height: 60px;
    width: 30%;
    background-color: transparent;

    display:flex;
    box-sizing: border-box;

    flex-grow: 0;
}
.editorMenubar input[type="file"]{
    display: none;
}
.sideLine{
    height: 45px;
    width: 1px;
    border-left: 1px solid gray;
    margin: 0 5px 0 5px;
}
/* f1 */

.container {
    background-color: #754d4d;
    position: fixed;/*있어야 밀림*/
    top: calc(100vh - 55px);
    bottom: 0;
    left: calc(20vw + 450px);
    height: 0;
    width: calc(200px);
    z-index: 50;
    display:flex;
  }
  
  .icon{
  font-size: 20pt; 
  margin: 0; 
  left:0px;
  display: block;
  border: 2px solid #202020;
  padding: 5px;
  width:45px;
  box-sizing: border-box;
  
  }
  
  .content {
  position: absolute;
  left:0px;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  }
  
  /* f2 */

.title{
    font-weight: 600;
    font-size: 20px;
}

.inputdesign{ 
    width:50px;
    height:30px;
    box-sizing: border-box;
    color:rgb(1, 110, 199);
    outline-style: none;
    padding-left: 5px;
    border-radius: 5px;
    background-color: transparent;
    border-color: transparent;
}
.inputdesign:hover{
    cursor:default;
}

.inputdesign::selection{
    color: #ffffff; 
    background:#666565;
}