 .Submenu {
    display:none;
    box-sizing: border-box;
    width: 200px;

    position:fixed;
    left: calc(50vw - 400px);
    bottom:50px;
    margin: 0px;
    color: black;
    user-select: none;
  }
  .layout{
    display: flex;
    flex-direction: row; 
    outline: none; 
  }
  .part{
    flex-direction: column;
    height: 50px;
   
  } 
.part input[type=radio]{
    display: none;
}
.part label{
   height: 0px;
   margin-top: 0px;
}
.part input[type=radio]+label{
  display: inline-block;
    cursor: pointer;
    height: 30px;
    width: 90px;
    border: 1px solid #D0D0D0;
    border-radius: 5px;
    line-height: 0px;
    text-align: center;
    font-weight:bold;
    font-size:16px;
    margin-right: 8px;
}
.part input[type=radio]+label{
    background-color: #fff;
    color: black;
}
.part input[type=radio]:checked+label{
    background-color: #D0D0D0;
    color: black;
}

  .Title {
    padding: 5px;
    text-align: center;
    background-color: #d0d0d0;
    border-radius: 5px;
  }

  .Submenu p {
    display: flex;
    align-content: space-between;
    justify-content: space-between;
    margin: 10px 15px 10px 15px;
  }

  .Submenu label {
    font-size: 12pt;
    padding-top:15px;
    padding-right: 5px;
  }

  .Submenu input[type="text"] {
    width: 50px;
    height: 30px;
    margin-top: 12px;
    box-sizing: border-box;
    color: black;
    outline-style: none;
    font-size: 17px;
    padding-left: 5px;    
    border-radius: 5px;
    background-color: #d0d0d0;
    border-color: transparent;
  }
  
  .Submenu input[type="color"] {
    width: 50px;
    height: 30px;
    margin-top: 12px;
    padding: 0;
    cursor: pointer;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: #d0d0d0;
    outline-style: none;
    border-color: transparent;  
  }
  
  
  .Submenu input[type="color"]::-webkit-color-swatch {
    border: none;
    border-radius: 5px;
    padding: 0;
  }
  
  .Submenu input[type="range"] {
    width: 150px;
    height: 18px;
    cursor: pointer;
    -webkit-appearance: none;
    user-select: none;
    background-color: transparent;
    outline: none;
    margin-right: 10px;
  }
  
  .Submenu input[type="range"]::-webkit-slider-runnable-track {
    border: none;
    border-radius: 5px;
    height: 7px;
    background: #a6a5a5;
    user-select: none;
  }
  
  .Submenu input[type="range"]::-webkit-slider-thumb {
    border: 1px solid #666565;
    height: 15px;
    width: 18px;
    border-radius: 10px;
    background: #d0d0d0;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -4px;
  }
  
  .Submenu select {
    height: 30px;
    cursor: pointer;
    background-color: #d0d0d0;
    border-color: transparent;
    color: black;
    outline: none;
    list-style: none;
  }
  
  .Submenu ul {
    display: flex;
    background-color: transparent;
    margin: auto;
    list-style: none;
    padding-left: 0;
    align-content: center;
    justify-content: flex-start;
    margin: 10px 15px 10px 15px;
  }
  
  .Submenu label input[type="file"] {
    display: none;
    margin: 0;
  }
  
  .Submenu button {
    outline: none;
    cursor: pointer;
    border: 0;
    width: 50px;
    height: 20px;
    border-radius: 5px;
    background-color: #b6b5b5;
    user-select: none;
    color: #161616;
    font-weight: bold;
  }
  
  .Submenu button:hover {
    background-color: white;
    box-shadow: inset 1px 0 #ddd, inset -1px 0 #ddd;
    transition: 0.3s;
  }
  
  .effectContainer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    /* 2개마다 오른쪽 열로 */
    align-items: center;
    /*가운데 정렬*/
    background-color: transparent;
  }

  .effectContainer button {
    width: 80px;
    margin: 3pt 0 3pt 0;
  }