.header{ 
    position: fixed; 
    left:0;
    top:3vh;
    width:50%;
    height: 50px;
    background-color: transparent;
    white-space: nowrap;
    min-width: 1000px;
}
.contents{
    font-family: Century Gothic, sans-serif;
    font-size: 50px;
    color:black;
    display:flex;  
    width:90%;    
    height:100%; 
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}
.corner{
    position: fixed;
}
.header:hover{
    cursor: default;
    user-select: none;
}

