.icon{
    font-size: 20pt; 
    margin: 0;
    left:0px;
    padding: 10px;
    width:48px;
    box-sizing: border-box;
    color: gray;
    border-radius: 5px;
    user-select: none;
  }
.icon:focus{
  outline:none;
}
.icon:hover{
  cursor: pointer;
  background-color: #fff;
  box-shadow: inset 1px 0 #ddd, inset -1px 0 #ddd;
  transition: 0.3s ;
}
.container:hover span{
  visibility: visible;
}
.container{
  position: relative;
  z-index: 100;
}
.container input[type="checkbox"]:checked ~ .icon{
  background-color: #ffffff;
}

/* 커서 올리면 나오는 문구 */
.tooltiptext{
  background-color: #D0D0D0;
  position: absolute;
  z-index: 100;

  visibility: hidden;
  border-radius: 5px;
  padding: 0 5px 0 5px;
  
  text-align: center;
  white-space: nowrap;

  bottom: 47px;
  left:0;
}